body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

line.tick {
  stroke: black;
  stroke-width: 1px;
}

.x-axis .tick-label {
  font-size: 10px;
}

.y-axis .tick-label {
  font-size: 14px;
}

.x-axis .gridline {
  stroke: #ddd;
  stroke-width: 1px;
}

.y-axis .gridline {
  stroke: #eee;
  stroke-width: 1px;
}

.plot line {
  stroke: black;
  stroke-width: 1px;
}

